//AjaxApiToken
function PollAjax(ajaxPram){
    function tryError(err) {
        switch (err.statusText) {
            case "Error":
            //gtUI.notification.error({message:"",description:"Error", duration:outTime});
            gtUI.message({message: 'Error',type: 'error'});
            break;
            case "Not Authenticated":
                //tokentoken
                //
                Vue.http.get("/Token/CleanToken",{token:token});
                //token
                location.reload();
                break;
                case "Internal Server Error":
                //gtUI.notification.error({message:"",description:err.data.Message, duration:outTime});
                gtUI.message({message: '',type: 'error'});
                break;
                case "No Transport":
                //gtUI.notification.error({message:"",description:"No Transport", duration:outTime});
                gtUI.message({message: err.data.Message,type: 'error'});
                break;
                case "Token Error":
                //gtUI.notification.error({message:"",description:"", duration:outTime});
                gtUI.message({message: "",type: 'error'});
                break;
                case "Host Error":
                //gtUI.notification.error({message:"",description:"", duration:outTime});
                gtUI.message({message: "",type: 'error'});
                break;

                default:
                // gtUI.notification.error({message:"",description:err.message, duration:outTime});
                gtUI.message({message: err.message,type: 'error'});
                //ajaxPram.error(err);
            }
        };

    //
    //
    let host = null;
    let token = null;
    //--------------------------
    //host = "http://192.168.1.152:8077/API";
    // Vue.http.get("/Settings/GetApiHost").then((result)=>{
    //             host = result.data;
    //     });
    $.ajax({
        url: "/Settings/GetApiHost",
        //data: param,
        type: "get",
        async: false,
        //cache: false,
        success: function (data) {
            host = data;
        }
    });
    if(!host){
        tryError("Host Error");
        return;
    }
    
    //Token
    // Vue.http.get("/Token/GetToken").then((result)=>{
    //             token = result.data;
    //     });
    $.ajax({
        url: "/Token/GetToken",
        //data: param,
        type: "get",
        async: false,
        //cache: false,
        success: function (data) {
            token = data;
        }
    });
    if(!token){
        tryError("Token Error");
        return;
    }
    // else{
    //     Vue.http.headers.common.Authorization = "Bearer " + token;
    // }
    // if(ajaxPram.async){
    //         //----------------------------
    //         if(ajaxPram.type.toLowerCase()=="get"){
    //             let pram = null;
    //             for(let item in ajaxPram.data){
    //                 if(pram) pram += ("&"+ item +"="+ ajaxPram.data[item]);
    //                 else pram = ("?" +item +"="+ ajaxPram.data[item]);
    //             }
    //             Vue.http.get(host+ajaxPram.url+pram).then((result)=>{
    //                 ajaxPram.success(result);
    //             }).catch(function(result){
    //                 tryError(result);
    //             })
    //         }


    //         if(ajaxPram.type.toLowerCase()=="post"){
    //             Vue.http.post(host+ajaxPram.url,ajaxPram.data).then((result)=>{
    //                 ajaxPram.success(result);
    //             }).catch(function(result){
    //                 tryError(result);
    //             })
    //         }
    //     }else{
            $.ajax({
                url: host+ajaxPram.url,
                type: ajaxPram.type,
                data: ajaxPram.data,
                contentType: ajaxPram.contentType != null? ajaxPram.contentType:"application/x-www-form-urlencoded",
                dataType: ajaxPram.dataType,
                async: ajaxPram.async != null ? ajaxPram.async : true,
                headers: {
                    "Authorization": "Bearer " + token
                },
                success: ajaxPram.success,
                error:function(ex){
                    gtUI.message({message: "'"+ajaxPram.url+"' - "+JSON.parse(ex.responseText).message,type: 'error'});
                    //tryError(ex.statusText);
                }
            });
        // }

}

export default  PollAjax;


